import { Component } from 'react'
import { View, Text, Button } from '@tarojs/components'
import './index.css'


export default class Index extends Component {

  componentWillMount() { }

  componentDidMount() { }

  componentWillUnmount() { }

  componentDidShow() { }

  componentDidHide() { }

  handleCancle = () => {
    this.props.changeHidden()
  }

  handleConfirm = (id) => {
    /* const {QQ} =e.target.value */
    //传输数据
    this.props.changeHidden()
    this.props.sellgood(id)
  }

  render() {
    const { hidden, text, id } = this.props
    return (
      <View>
        {hidden ? null : <View class='toast-box' >
          <View class='toastbg'></View>
          <View class='showToast'>
            <View class='toast-title'>
              <Text>{text}</Text>
            </View>
            <View class='toast-main'>
            </View>
            <View class='toast-button'>
              <View class='button1'>
                <Button catchtap='cancel' onClick={this.handleCancle}>取消</Button>
              </View>
              <View class='button2'>
                <Button catchtap='confirm' onClick={() => this.handleConfirm(id)}>确定</Button>
              </View>
            </View>
          </View>
        </View>}
      </View>
    )
  }
}